  legend="Input component for numerical value"
/>

<section>
  <Example title="example" code={exampleCode.example}>
    <Form>
      <NumberInput bind:value={value} label="Input" />
    </Form>
  </Example>

  <Example title="controls" code={exampleCode.controls}>
    <Form class="form-col">
      <NumberInput
        bind:value={value1}
        label="Min - Max boundaries"
        min={0}
        max={10}
      />
      <NumberInput
        bind:value={value2}
        label="Step"
        step={100}
      />
      <NumberInput
        bind:value={value1}
        label="No controls"
        controls={false}
      />
      <NumberInput
        bind:value={value1}
        label="Controls type"
        controlsType="primary"
      />
    </Form>
  </Example>

  <Example title="formatting" code={exampleCode.formatting}>
