<form {...$$restProps}
      class="{$$restProps.class ?? ''} form-group"
      on:submit|preventDefault
      on:click
      on:keydown
      on:mouseover
      on:mouseenter
      on:mouseleave
      on:focus>
  <slot />
</form>

<style >form {
  /* NOTE: set back PaperCSS margins for our structure
    <label> being parent of <input> instead of a sibling */
}
form :global(label) {
  margin-block-end: 0;
}
form :global(input) {
  margin-block-start: 0.5rem;
}</style>
